.landingContent{
    position: relative;
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 70%, rgba(0, 0, 0, 1)), url('../../assets/bar.jpg');
    min-height: 100vh;
    width: 100%;

    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    animation: breath 10s linear infinite;
    overflow: hidden;
}

@keyframes breath {
    0% { opacity: 0; }
    5% { opacity: 0; }
    10% { opacity: 1; }
    50% { opacity: 1;}
    90% { opacity: 1; }
    95% { opacity: 0; }
    100% { opacity: 0;}      
}

