.landingTxtContainer{
    position: absolute;
    left: 50%;
    bottom: 0px;
    transform: translateX(-50%) translateY(-50%);
    text-align: center;
    color: whitesmoke;
}

@media screen and (max-width: 700px){

    @keyframes breath {
        0% { background-size: cover; opacity: 0; }
        5% { opacity: 0; }
        10% { opacity: 1; }
        50% {  background-size: cover; opacity: 1;}
        90% { opacity: 1; }
        95% { opacity: 0; }
        100% {  background-size: cover; opacity: 0;}      
    }
}

