@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@400&display=swap);
html {

  scroll-behavior: smooth;
}

body {
  margin: 0;
  background-color: black;
}

h1, h2, h3, h4 {
  font-family: 'Dosis', sans-serif;
}

p{
  font-family: 'Open Sans', sans-serif;
}

.mySlides {display: block}
.Landing_landingContent__ZokZL{
    position: relative;
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 70%, rgba(0, 0, 0, 1)), url(/pub/static/media/bar.50aca90b.jpg);
    min-height: 100vh;
    width: 100%;

    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    -webkit-animation: Landing_breath__1wer_ 10s linear infinite;
            animation: Landing_breath__1wer_ 10s linear infinite;
    overflow: hidden;
}

@-webkit-keyframes Landing_breath__1wer_ {
    0% { opacity: 0; }
    5% { opacity: 0; }
    10% { opacity: 1; }
    50% { opacity: 1;}
    90% { opacity: 1; }
    95% { opacity: 0; }
    100% { opacity: 0;}      
}

@keyframes Landing_breath__1wer_ {
    0% { opacity: 0; }
    5% { opacity: 0; }
    10% { opacity: 1; }
    50% { opacity: 1;}
    90% { opacity: 1; }
    95% { opacity: 0; }
    100% { opacity: 0;}      
}

